import React, { useContext, useState, useEffect } from 'react';
import '../style/events.css'
import { Formik, Field, Form, ErrorMessage } from 'formik';
import * as Yup from 'yup';
import * as Datetime from 'react-datetime';
import moment from 'moment';
import { AuthContext } from '../authContext';


function Event({ match, history }) {

    const [editMode, setEditMode] = useState(false);
    const [event, setEvent] = useState([]);

    const [loading, setLoading] = useState(true);

    const status = ["Failed", "Ongoing", "Done"];

    const [validate, setValidate] = useState(true);

    const cont = useContext(AuthContext);

    useEffect(() => {
        async function fetchEvent() {
            if (!cont.user)
                history.push("/unauthorized");
            else {
                try {
                    let response = await fetch(`/api/v1/events/${match.params.id}`, {
                        method: "GET",
                        headers: {
                            'Accept': 'application/json',
                            'Content-Type': 'application/json'
                        }
                    });
                    console.log(response);

                    if (response.status === 200) {
                        const json = await response.json();
                        setEvent(json);
                        setEditMode(false);
                        setLoading(false)
                    } else if (response.status === 400)
                        history.push('/wrongreq')
                    else if (response.status === 404)
                        history.push('/notfound')
                } catch (error) {
                    console.log(error);
                }
            }
        }

        if (match.params.id !== null) {
            fetchEvent();
        }
    }, [match.params.id, history, cont.user]);

    function prevDef(event) {
        event.preventDefault();
        setEditMode(!editMode);
    }

    let yesterday = Datetime.moment().subtract(1, 'day');

    let valid = function (current) {
        return current.isAfter(yesterday);
    };

    let ttime = moment(event.deadLine).format("MM/DD/YYYY h:mm A");

    function val(inputDate) {
        if (moment(inputDate).isAfter(moment()))
            setValidate(true);
        else
            setValidate(false);
    }

    async function deleteEvent() {
        try {
            let response = await fetch(`/api/v1/events/${match.params.id}`, {
                method: "DELETE",
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                }
            });
            console.log(response);
            if (response.status === 204) {
                history.push("/events");
            } else if (response.status === 400) {
                history.push("/wrongreq");
            }
        } catch (err) {
            console.log(err);
        }
    }

    return (
        <div> {!loading &&
            < Formik
                enableReinitialize={true}
                initialValues={{
                    description: event.description,
                    name: event.name,
                    difficulty: event.difficulty,
                    status: status[event.status + 1],
                    deadline: event.deadline
                }}
                validationSchema={
                    Yup.object().shape({
                        description: Yup.string()
                            .min(10, 'Bio must be at least 10 characters')
                            .max(400, 'Bio shoudn`t be above 400 characters')
                            .required(),
                        name: Yup.string()
                            .min(4, "Name must be at least 10 characters")
                            .max(20, 'Name shoudn`t be above 20 characters')
                            .required(),
                        difficulty: Yup.number()
                            .min(1, 'Difficulty should be from 1 to 10')
                            .max(10, 'Difficulty should be from 1 to 10')
                            .required(),
                    })
                }
                onSubmit={async (fields) => {

                    try {
                        let statusInt = 0;

                        if (fields.status === "Done")
                            statusInt = 1;

                        let time = moment(fields.deadline).format();
                        console.log(time);

                        let formData = new FormData();
                        if (fields.file)
                            formData.append('themeUrl', fields.file);
                        formData.append('status', statusInt);
                        formData.append('description', fields.description);
                        formData.append('name', fields.name);
                        formData.append('difficulty', fields.difficulty);
                        formData.append('deadline', time);
                        setLoading(true);
                        let resualt = await fetch(`/api/v1/events/${match.params.id}`, {
                            method: "PUT",
                            headers: {
                            },
                            body: formData
                        })
                        console.log(resualt);
                        if (resualt.status === 200) {
                            let json = await resualt.json();
                            setEditMode(false);
                            setEvent(json);
                            setLoading(false);
                        } else if (resualt.status === 400) {
                            history.push('/wrongreq')
                        } else if (resualt.status === 404) {
                            history.push('/notfound')
                        }
                    } catch (err) {
                        console.log(err);
                    }

                }}
            >
                {({ errors, touched, setFieldValue }) => {
                    return (
                        <Form>
                            <div className="EventWrapper">
                                <div className="LeftInfoBlockEvent">
                                    <img src={event.themeUrl} alt="Evnent theme"></img>
                                    <div className="EventChangeTheme">
                                        {editMode ? <input type="file" name="file" onChange={(event) => { setFieldValue("file", event.currentTarget.files[0]); }}></input> : <div />}
                                    </div>
                                    <div className="NameInfo">
                                        <label htmlFor="name"><p>Name&nbsp;:&nbsp;</p></label>
                                        {editMode ? <Field name="name" className="form-control" component="input" /> : <p>{event.name}</p>}
                                        <ErrorMessage component="div" className="invalid-feedback error-message ErrorName" name="name" />
                                    </div>
                                    <div className="DifficultyInfo">
                                        <label htmlFor="difficulty"><p>Difficulty&nbsp;:&nbsp;</p></label>
                                        {editMode ? <input name="difficulty" defaultValue={event.difficulty} onChange={(event) => { setFieldValue("difficulty", event.currentTarget.value); }} className="form-control" type="number" min="1" max="10" /> : <p>{event.difficulty}</p>}
                                    </div>
                                    <div className="StatusInfo">
                                        <label htmlFor="status">Status&nbsp;:&nbsp;</label>
                                        {editMode ? <Field name="status" className="form-control" component="select"><option>Ongoing</option><option>Done</option></Field> : <p>{status[event.status + 1]}</p>}
                                    </div>
                                    <div className="DeadlineInfo">
                                        <label id="date" htmlFor="deadline">Deadline&nbsp;:&nbsp;</label>
                                        {editMode ? <Datetime isValidDate={valid} defaultValue={ttime} inputProps={{ className: 'datetime', disabled: true }} onChange={(date) => { setFieldValue("deadline", date._d); val(date._d) }} /> : <p>{ttime}</p>}
                                    </div>
                                    {!editMode &&
                                        <div className="DeleteButton">
                                            <button type="button" class="btn btn-info btn-lg btn-danger" data-toggle="modal" data-target="#myModal">Delete</button>
                                            <div class="modal fade" id="myModal" role="dialog">
                                                <div class="modal-dialog modal-sm">
                                                    <div class="modal-content">
                                                        <div class="modal-header">
                                                            <button type="button" class="close" data-dismiss="modal">&times;</button>
                                                            <h4 class="modal-title">Are you sure?</h4>
                                                            <div class="modal-footer">
                                                                <button type="button" onClick={() => { deleteEvent() }} class="btn btn-default" data-dismiss="modal" method="POST">Ok</button>
                                                                <button type="button" class="btn btn-default" data-dismiss="modal">Close</button>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>}
                                </div>
                                <div className="RightInfoBlockEvent">
                                    <h1>Description</h1>
                                    {editMode ? <Field name="description" className="form-control" component="textarea"></Field> : <p>{event.description}</p>}
                                    <ErrorMessage component="div" className="invalid-feedback error-message" name="description" />
                                    {event.status === 0 && editMode && validate ? <button style={{backgroundColor : "#4ACF93"}} type="submit" className="form-control">Save</button> : (editMode && !validate && event.status === 0) ? <button disabled type="submit" className="form-control">Save</button> : (event.status === 0) ? <button style={{backgroundColor: "yellow"}} className="form-control" type="button" onClick={prevDef}>Edit Mode</button> : <div></div>}
                                </div>
                            </div>
                        </Form>

                    );
                }}
            </Formik >

        }

        </div>
    );
}



export default Event;