import React from 'react';
import Nav from './components/Nav';
import About from './components/About';
import Home from './components/Home';
import Login from './components/Login'
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import {AuthProvider} from './authContext';
import Footer from './components/Footer';
import Register from './components/Register';
import Profile from './components/Profile';
import Users from './components/Users';
import Events from './components/Events';
import Event from './components/Event';
import AddEvent from './components/AddEvent';
import AddCallendar from './components/AddCalendar';
import Calendar from './components/Calendar';
import Calendars from './components/Calendars'
import NotFound from './components/ErrorComp/NotFound';
import WrongReq from './components/ErrorComp/WrongReq';
import Forbiden from './components/ErrorComp/Forbiden';
import Unauthorized from './components/ErrorComp/Unauthorized';





 

function App() {
  return (
    <AuthProvider>
    <Router >
        <div>
            <Nav />
          <Switch>
            <Route path="/" exact component={Home} />
            <Route path="/events" exact component={Events} />
            <Route path="/calendars" exact component={Calendars} />
            <Route path="/AddCalendar" exact component={AddCallendar} />
            <Route path="/about" exact component={About} />
            <Route path="/addEvent" exact component={AddEvent} />
            <Route path="/login" exact component={Login} />
            <Route path="/register" exact component={Register} />
            <Route path="/profile/:id"  exact component={Profile} />
            <Route path="/event/:id" exact component={Event} />
            <Route path="/calendar/:id" exact component={Calendar} />
            <Route path="/wrongreq" exact component={WrongReq} />
            <Route path="/users" exact component={Users} />
            <Route path="/forbiden" exact component={Forbiden} />
            <Route path="/unauthorized" exact component={Unauthorized} />
            <Route path="/" component={NotFound} />
          </Switch>
            <Footer />
        </div>  
    </Router> 
    </AuthProvider>
    
  );
}

export default App;


