import React, { useContext, useState, useEffect } from 'react';
import '../style/users.css'
import User from './User';
import { AuthContext } from '../authContext';



function Users({ history }) {
  const cont = useContext(AuthContext);


  const [users, setUsers] = useState([]);
  const [page, setPage] = useState(1);
  const [allUsersCount, setAllUsersCount] = useState();
  const [loading, setLoading] = useState(true);
  const [search, setSearch] = useState("");
  useEffect(() => {
    async function fetchUsers() {
      if (!cont.user)
        history.push('/unauthorized');
      else if (cont.user.role !== 1)
      history.push('/forbiden');
      else {
        try {
          let response = await fetch(`/api/v1/users/?page=1&search=${search}`, {
            method: "GET",
            headers: {
              'Accept': 'application/json',
              'Content-Type': 'application/json'
            }
          });
          if (response.status === 200) {
            const json = await response.json();
            setUsers(json.users);
            setAllUsersCount(json.count);
            setLoading(false);
          } else if (response.status === 404)
            history.push('/notfound');
          else if (response.status === 400)
            history.push('/wrongreq');
        } catch (error) {
          console.log(error);
        }
      }
    }
    fetchUsers();

  }, [setAllUsersCount, history, search, cont.user]);

  async function nextPageEvent(page) {
    setLoading(true);
    let nextPage = parseInt(page) + 1;
    console.log(nextPage, "page");
    try {
      let response = await fetch(`/api/v1/users/?page=${nextPage}&search=${search}`, {
        method: "GET",
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json'
        }
      });
      if (response.status === 200) {
        const json = await response.json();
        setUsers(json.users);
        setAllUsersCount(json.count);
        setPage(nextPage);
        setLoading(false);
      } else if (response.status === 404)
        history.push('/notfound');
      else if (response.status === 400)
        history.push('/wrongreq');
    } catch (err) {
      console.log(err);
    }
  }

  async function prevPageEvent(page) {
    setLoading(true);
    let prevPage = parseInt(page) - 1;
    console.log(prevPage, "page");
    try {
      let response = await fetch(`/api/v1/users/?page=${prevPage}&search=${search}`, {
        method: "GET",
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json'
        }
      });
      if (response.status === 200) {
        const json = await response.json();
        setUsers(json.users);
        setAllUsersCount(json.count);
        setPage(prevPage);
        setLoading(false)
      } else if (response.status === 404)
        history.push('/notfound');
      else if (response.status === 400)
        history.push('/wrongreq');
    } catch (err) {
      console.log(err);
    }
  }

  const maxPage = Math.ceil(allUsersCount / 5);
  console.log(allUsersCount);
  return (
    <div> {!loading &&
      <div className="UsersWrapper">
        <div className="SearchBlock">
          <label>Search&nbsp;by&nbsp;Login&nbsp;:&nbsp;</label>
          <input className="form-control Search" maxLength="20" onChange={(event) => { event.preventDefault(); setSearch(event.currentTarget.value); }}></input>
        </div>
        {allUsersCount === 0 ? <div className="NothingFoundCol">Nothing&nbsp;Found</div> :
          <div>
            <div className="UsersInfo">
              <table className="table table-hover table-fixed">
                <thead>
                  <tr>
                    <th>#</th>
                    <th>Login</th>
                    <th>RegisteredAt</th>
                  </tr>
                </thead>
                <tbody>
                  {users.map((item, index) => (
                    <User key={item._id} index={index} page={page} item={item} />
                  ))}
                </tbody>
              </table>
            </div>
            <div className="PaginationButtons">
              <div className="prevButton">
                {(page === 1) ? <button disabled className="form-control">prev</button> : <button className="form-control" onClick={() => { prevPageEvent(page) }}>prev</button>}
              </div>
              <p>{page + ' / ' + maxPage}</p>
              <div className="nextButton">
                {(((page) * 5) < allUsersCount) ? <button className="form-control" onClick={() => { nextPageEvent(page) }}>next</button> : <button disabled className="form-control">next</button>}
              </div>
            </div>
          </div>}
      </div>}
    </div>
  );
}

export default Users;