import React, { useContext, useState, useEffect } from 'react';
import '../style/calendars.css';
import CalendarTableItem from './CalendarTableItem';
import { AuthContext } from '../authContext';


function Calendars({ history }) {

    const [calendars, setCalendars] = useState([]);
    const [page, setPage] = useState(1);
    const [allCalendarsCount, setAllCalendarsCount] = useState();
    const [loading, setLoading] = useState(true);
    const [search, setSearch] = useState("");
    const cont = useContext(AuthContext);
    useEffect(() => {
        async function fetchCalendars() {
            if (!cont.user)
                history.push("/unauthorized");
            else {
                try {
                    let response = await fetch(`/api/v1/calendars/?page=1&search=${search}`, {
                        method: "GET",
                        headers: {
                            'Accept': 'application/json',
                            'Content-Type': 'application/json'
                        }
                    });
                    console.log(response);

                    if (response.status === 200) {
                        const json = await response.json();
                        console.log(json);
                        setCalendars(json.calendars);
                        setAllCalendarsCount(json.count);
                        setLoading(false);
                    } else if (response.status === 404)
                        history.push('/notfound');
                    else if (response.status === 400)
                        history.push('/wrongreq');
                } catch (error) {
                    console.log(error);
                }
            }
        }
        fetchCalendars();

    }, [history, search, cont.user]);

    async function nextPageCalendars(page) {
        setLoading(true);
        let nextPage = parseInt(page) + 1;
        console.log(nextPage, "page");
        try {
            let response = await fetch(`/api/v1/calendars/?page=${nextPage}&search=${search}`, {
                method: "GET",
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                }
            });
            if (response.status === 200) {
                const json = await response.json();
                setCalendars(json.calendars);
                setAllCalendarsCount(json.count);
                setPage(nextPage);
                setLoading(false);
            } else if (response.status === 404)
                history.push('/notfound');
            else if (response.status === 400)
                history.push('/wrongreq');
        } catch (err) {
            console.log(err);
        }
    }


    async function prevPageCalendars(page) {
        setLoading(true);
        let prevPage = parseInt(page) - 1;
        console.log(prevPage, "page");
        try {
            let response = await fetch(`/api/v1/calendars/?page=${prevPage}&search=${search}`, {
                method: "GET",
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                }
            });
            if (response.status === 200) {
                const json = await response.json();
                setCalendars(json.calendars);
                setAllCalendarsCount(json.count);
                setPage(prevPage);
                setLoading(false);
            } else if (response.status === 404)
                history.push('/notfound');
            else if (response.status === 400)
                history.push('/wrongreq');
        } catch (err) {
            console.log(err);
        }
    }


    const maxPage = Math.ceil(allCalendarsCount / 5);
    return (
        <div> {!loading &&
            <div className="EventsWrapper">
                <div className="SearchBlock">
                    <label>Search&nbsp;by&nbsp;Name&nbsp;:&nbsp;</label>
                    <input className="form-control Search" maxLength="20" onChange={(event) => { event.preventDefault(); setSearch(event.currentTarget.value); }}></input>
                </div>
                <div className="EventsInfo">
                    {allCalendarsCount === 0 ? <div className="NothingFoundCol">Nothing&nbsp;Found</div> :
                        <div>
                            <table className="table table-hover table-fixed">
                                <thead>
                                    <tr>
                                        <th>#</th>
                                        <th>Name</th>
                                        <th>Creation Date</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {calendars.map((item, index) => (
                                        <CalendarTableItem key={item._id} index={index} page={page} item={item} />
                                    ))}
                                </tbody>
                            </table>
                            <div className="PaginationButtons">
                                <div className="prevButton">
                                    {(page === 1) ? <button disabled className="form-control">prev</button> : <button className="form-control" onClick={() => { prevPageCalendars(page) }} >prev</button>}
                                </div>
                                <p>{page + ' / ' + maxPage}</p>
                                <div className="nextButton">
                                    {(((page) * 5) < allCalendarsCount) ? <button className="form-control" onClick={() => { nextPageCalendars(page) }}>next</button> : <button disabled className="form-control">next</button>}
                                </div>
                            </div>
                        </div>}
                    <div className="AddButton">
                        <button className="form-control" onClick={() => { history.push("/addCalendar") }}>Add calendar</button>
                    </div>
                </div>
            </div>}
        </div>
    );
}



export default Calendars;
