import React, { useReducer } from 'react';
// import history from './history';

const AuthContext = React.createContext();

function getInitialState() {
    console.log(localStorage.getItem('curUser'), "sldasdasd");
    if (localStorage.getItem('curUser')) {
        return { user: JSON.parse(localStorage.getItem('curUser')) }
    } else {
        return { user: null }
    }
}

let reducer = (state, action) => {
    switch (action.type) {
        case 'LogInUser':
            return {
                ...state,
                user: action.payload
            }
        case 'LogOutUser':
            return {
                ...state,
                user: action.payload
            }
        default:
            return state;
    }
}

const AuthProvider = props => {
    const initalState = getInitialState();
    const [state, dispatch] = useReducer(reducer, initalState);

    return (

        <AuthContext.Provider value={{
            ...state,
            handleLogIn: (values) => { dispatch({ type: 'LogInUser', payload: values.user }); localStorage.setItem('curUser', JSON.stringify(values.user)); },
            handleLogOut: values => { dispatch({ type: 'LogOutUser', payload: null }); localStorage.removeItem('curUser');}
        }} >
            {props.children}
        </AuthContext.Provider>
    )
}

export { AuthContext, AuthProvider };