import React, { useContext, useState, useEffect } from 'react';
import '../style/events.css'
import EventTableItem from './EventTableItem';
import { AuthContext } from '../authContext';


function Events({ history }) {

    const [events, setEvents] = useState([]);
    const [page, setPage] = useState(1);
    const [allEventsCount, setAllEventsCount] = useState();
    const [loading, setLoading] = useState(true);
    const [search, setSearch] = useState("");
    const cont = useContext(AuthContext);


    useEffect(() => {
        async function fetchEvents() {
            if (!cont.user)
                history.push('/unauthorized');
            else {
                try {
                    let response = await fetch(`/api/v1/events/?page=1&search=${search}`, {
                        method: "GET",
                        headers: {
                            'Accept': 'application/json',
                            'Content-Type': 'application/json'
                        }
                    });

                    if (response.status === 200) {
                        const json = await response.json();
                        console.log(json);
                        setEvents(json.events);
                        setAllEventsCount(json.count);
                        setLoading(false);
                    } else if (response.status === 404)
                        history.push('/notfound');
                    else if (response.status === 400)
                        history.push('/wrongreq');
                } catch (error) {
                    console.log(error);
                }
            }
        }
        fetchEvents();

    }, [setAllEventsCount, history, search, cont.user]);

    async function nextPageEvent(page) {
        setLoading(true);
        let nextPage = parseInt(page) + 1;
        console.log(nextPage, "page");
        try {
            let response = await fetch(`/api/v1/events/?page=${nextPage}&search=${search}`, {
                method: "GET",
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                }
            });
            if (response.status === 200) {
                const json = await response.json();
                setEvents(json.events);
                setAllEventsCount(json.count);
                setPage(nextPage)
                setLoading(false);
            } else if (response.status === 404)
                history.push('/notfound');
            else if (response.status === 400)
                history.push('/wrongreq');
        } catch (err) {
            console.log(err);
        }
    }

    async function prevPageEvent(page) {
        setLoading(true);
        let prevPage = parseInt(page) - 1;
        console.log(prevPage, "page");
        try {
            let response = await fetch(`/api/v1/events/?page=${prevPage}&search=${search}`, {
                method: "GET",
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                }
            });
            if (response.status === 200) {
                const json = await response.json();
                setEvents(json.events);
                setAllEventsCount(json.count);
                setPage(prevPage);
                setLoading(false);
            } else if (response.status === 404)
                history.push('/notfound');
            else if (response.status === 400)
                history.push('/wrongreq');
        } catch (err) {
            console.log(err);
        }
    }

    const maxPage = Math.ceil(allEventsCount / 5);

    return (
        <div> {!loading &&
            <div className="EventsWrapper">
                <div className="SearchBlock">
                    <label>Search&nbsp;by&nbsp;Name&nbsp;:&nbsp;</label>
                    <input className="form-control Search" maxLength="20" onChange={(event) => { event.preventDefault(); setSearch(event.currentTarget.value); }}></input>
                </div>
                <div className="EventsInfo">
                    {(allEventsCount === 0) ? <div className="NothingFoundCol">Nothing&nbsp;Found</div> :
                        <div>
                            <table className="table table-hover table-fixed">
                                <thead>
                                    <tr>
                                        <th>#</th>
                                        <th>Name</th>
                                        <th>Deadline</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {events.map((item, index) => (
                                        <EventTableItem key={item._id} index={index} page={page} item={item} />
                                    ))}
                                </tbody>
                            </table>
                            <div className="PaginationButtons">
                                <div className="prevButton">
                                    {(page === 1) ? <button disabled className="form-control">prev</button> : <button className="form-control" onClick={() => { prevPageEvent(page) }} >prev</button>}
                                </div>
                                <p>{page + ' / ' + maxPage}</p>
                                <div className="nextButton">
                                    {(((page) * 5) < allEventsCount) ? <button className="form-control" onClick={() => { nextPageEvent(page) }}>next</button> : <button disabled className="form-control">next</button>}
                                </div>
                            </div>
                        </div>}
                    <div className="AddButton">
                        <button className="form-control" onClick={() => { history.push("/addEvent") }}>Add event</button>
                    </div>
                </div>
            </div>}
        </div>
    );
}



export default Events;