import React, { useContext, useState, useEffect } from 'react';
import '../style/footer.css';
import { Formik, Form, Field, ErrorMessage } from 'formik';
import * as Yup from 'yup';
import { Link } from 'react-router-dom';
import { AuthContext } from '../authContext';


function Calendar({ match, history }) {

    const [editMode, setEditMode] = useState(false);
    const [calendar, setCalendar] = useState([]);
    const [allNotCalEv, setAllNotCalEv] = useState([]);
    const [eventToLink, setEventToLink] = useState();
    const [eventToUnlink, setEventToUnLink] = useState();

    const [loadData, setLoadData] = useState(false);

    const [events, setEvents] = useState([]);
    const [pageCalEv, setPageCalEv] = useState(1);
    const [pageCalEvCount, setPageCalEvCount] = useState(0);

    const [notAssignedEvents, setNotAssignedEvents] = useState([]);
    const [pageNotCalEv, setPageNotCalEv] = useState(1);
    const [pageNotCalEvCount, setPageNotCalEvCount] = useState(0);

    const [loading, setLoading] = useState(true);
    const cont = useContext(AuthContext);

    async function handleAddLink() {
        try {

            let response = await fetch(`/api/v1/links`, {
                method: "POST",
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({ eventId: eventToLink, calendarId: calendar._id })
            });
            console.log(response);
            if (response.status === 201) {
                setLoadData(true);
            } else if (response.status === 400) {
                history.push("/wrongreq");
            } else if (response.status === 500) {
                console.log("pizda");
            }
        } catch (err) {
            console.log(err);
        }
    }

    async function handleDeleteLink() {
        try {
            let response = await fetch(`/api/v1/links`, {
                method: "DELETE",
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({ eventId: eventToUnlink, calendarId: calendar._id })
            });
            console.log(response);
            if (response.status === 204) {
                setLoadData(true);
            } else if (response.status === 400) {
                history.push("/wrongreq");
            } else if (response.status === 500) {
                console.log("pizda");
            }
        } catch (err) {
            console.log(err);
        }
    }

    function getEvents(page, ev) {
        let pagEvents = [];
        for (let i = (page - 1) * 3; i < ((page) * 3); i++) {
            if (ev[i])
                pagEvents.push(ev[i]);
        }
        return pagEvents;
    }

    function nextPageEvCal() {
        let nextPage = parseInt(pageCalEv) + 1;
        setEvents(getEvents(nextPage, calendar.events));
        setPageCalEv(nextPage);
    }

    function nextPageNotEvCal() {
        let nextPage = parseInt(pageNotCalEv) + 1;
        setNotAssignedEvents(getEvents(nextPage, allNotCalEv));
        setPageNotCalEv(nextPage);
    }

    function prevPageEvCal() {
        let prevPage = parseInt(pageCalEv) - 1;
        setEvents(getEvents(prevPage, calendar.events));
        setPageCalEv(prevPage);
    }

    function prevPageNotEvCal() {
        let prevPage = parseInt(pageNotCalEv) - 1;
        setNotAssignedEvents(getEvents(prevPage, allNotCalEv));
        setPageNotCalEv(prevPage);
    }

    async function deleteCalendar() {
        try {
            let response = await fetch(`/api/v1/calendars/${match.params.id}`, {
                method: "DELETE",
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                }
            });
            console.log(response);
            if (response.status === 204) {
                history.push("/calendars")
            } else if (response.status === 400) {
                history.push("/wrongreq");
            } else if (response.status === 500) {
                console.log("pizda");
            }
        } catch (err) {
            console.log(err);
        }
    }

    useEffect(() => {
        async function fetchEvent() {
            if (!cont.user)
                history.push("/unauthorized");
            else {
                try {
                    let response = await fetch(`/api/v1/calendars/${match.params.id}`, {
                        method: "GET",
                        headers: {
                            'Accept': 'application/json',
                            'Content-Type': 'application/json'
                        }
                    });
                    console.log(response);


                    if (response.status === 200) {
                        const json = await response.json();
                        setCalendar(json.calendar);
                        setEvents(getEvents(1, json.calendar.events));
                        setPageCalEvCount(Math.ceil(json.calendar.events.length / 3));
                        setNotAssignedEvents(getEvents(1, json.notCalendarEvents));
                        setPageNotCalEvCount(Math.ceil(json.notCalendarEvents.length / 3));
                        setAllNotCalEv(json.notCalendarEvents);
                        setEditMode(false);
                        setLoading(false);
                        setLoadData(false);
                    } else if (response.status === 400)
                        history.push('/wrongreq')
                    else if (response.status === 404)
                        history.push('/notfound')
                }
                catch (error) {
                    console.log(error);
                }
            }
        }

        if (match.params.id !== null) {
            fetchEvent();
        }
    }, [match.params.id, history, loadData, cont.user]);

    function clearSelected() {
        let elements = document.getElementById("selectmy").options;

        for (let i = 0; i < elements.length; i++) {
            elements[i].selected = false;
        }
    }

    function clearSelected2() {
        let elements = document.getElementById("selectmy2").options;

        for (let i = 0; i < elements.length; i++) {
            elements[i].selected = false;
        }
    }

    function backCol(itemStatus) {
        if (itemStatus === -1)
            return "#FF4E4E";
        if (itemStatus === 1)
            return "#66FF66";
        else
            return "";
    }

    console.log(calendar.name);



    return (
        <div> {!loading &&
            < Formik
                enableReinitialize={true}
                initialValues={{
                    name: calendar.name
                }}
                validationSchema={
                    Yup.object().shape({
                        name: Yup.string()
                            .min(4, 'Name must be at least 10 characters')
                            .max(20, 'Name shoudn`t be above 20 characters')
                            .required()
                    })
                }
                onSubmit={async (fields) => {
                    try {
                        let formData = new FormData();
                        if (fields.file)
                            formData.append('photoUrl', fields.file);

                        formData.append("name", fields.name);
                        setLoading(true);
                        let resualt = await fetch(`/api/v1/calendars/${match.params.id}`, {
                            method: "PUT",
                            headers: {
                            },
                            body: formData
                        })
                        console.log(resualt);
                        if (resualt.status === 200) {
                            let json = await resualt.json();
                            setEditMode(false);
                            setCalendar(json);
                            setLoading(false);
                        } else if (resualt.status === 400) {
                            history.push('/wrongreq')
                        } else if (resualt.status === 404) {
                            history.push('/notfound')
                        }
                    } catch (err) {
                        console.log(err);
                    }


                }}
            >
                {({ errors, touched, setFieldValue }) => {
                    return (
                        <Form>
                            <div className="CalendarWrapper" >
                                <div className="CalendarLeftBlock">
                                    <img src={calendar.photoUrl} alt="photoUrl"></img>
                                    {editMode && <input type="file" name="file" onChange={(event) => { setFieldValue("file", event.currentTarget.files[0]); }}></input>}
                                    <div className="DeleteButtonCl">
                                        <button type="button" class="btn btn-info btn-lg btn-danger" data-toggle="modal" data-target="#myModal">Delete</button>
                                        <div class="modal fade" id="myModal" role="dialog">
                                            <div class="modal-dialog modal-sm">
                                                <div class="modal-content">
                                                    <div class="modal-header">
                                                        <button type="button" class="close" data-dismiss="modal">&times;</button>
                                                        <h4 class="modal-title">Are you sure?</h4>
                                                        <div class="modal-footer">
                                                            <button type="button" onClick={() => { deleteCalendar() }} class="btn btn-default" data-dismiss="modal" method="POST">Ok</button>
                                                            <button type="button" class="btn btn-default" data-dismiss="modal">Close</button>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="CalendarRighttBlock">
                                    <div className="NameInfoCl">
                                        <label>Name&nbsp;:&nbsp;</label>
                                        <div className="NameInfoClP">
                                            {editMode ? <Field name="name" type="text"></Field> : <p className="CalendarRighttBlockP">{calendar.name}</p>}
                                            <ErrorMessage component="div" className="invalid-feedback error-message" name="name" />
                                        </div>
                                    </div>
                                    <div className="CalendarEvents">
                                        <h4>Events</h4>
                                        {pageCalEvCount === 0 ? <div className="NothingFoundProfile">Nothing&nbsp;Found</div> :
                                            <ul className="list-group">
                                                {events.map((item, index) => (
                                                    <li style={{ background: backCol(item.status) }} className="list-group-item" key={index}><Link to={"/event/" + item._id}>{item.name}</Link></li>
                                                ))}
                                            </ul>}
                                    </div>
                                    {pageCalEvCount === 0 ? <div></div> :
                                        <div className="EventsButtonsCl">
                                            <div className="prevButtonClEv">
                                                {(pageCalEv === 1) ? <button disabled className="form-control">prev</button> : <button className="form-control" onClick={(event) => { event.preventDefault(); prevPageEvCal(); }} >prev</button>}
                                            </div>
                                            <p>{pageCalEv + ' / ' + pageCalEvCount}</p>
                                            <div className="nextButtonClEv">
                                                {((pageCalEv) < pageCalEvCount) ? <button className="form-control" type="button" onClick={(event) => { event.preventDefault(); nextPageEvCal(); }}>next</button> : <button disabled className="form-control">next</button>}
                                            </div>
                                        </div>}
                                    {pageCalEvCount === 0 ? <div></div> :
                                        <div className="DeleteLink">
                                            <select className="form-control" id="selectmy2" onChange={(event) => { event.preventDefault(); setEventToUnLink(event.currentTarget.value); }}>
                                                <option hidden defaultValue >Choose</option>
                                                {events.map((item, index) => (
                                                    <option key={index} value={item._id}>{item.name}</option>
                                                ))}
                                            </select>
                                        </div>}
                                    {pageCalEvCount === 0 ? <div></div> :
                                        <div className="AddButtonCL2">
                                            {eventToUnlink ? <button className="form-control" onClick={(event) => { event.preventDefault(); handleDeleteLink(); setLoadData(false); setEventToUnLink(undefined); clearSelected2(); }}>Delete Link</button> : <button disabled className="form-control">Delete Link</button>}
                                        </div>}
                                    {!editMode ? <button style={{backgroundColor: "yellow"}} className="form-control EditMode" type="button" onClick={(event) => { event.preventDefault(); setEditMode(true) }}>Edit Mode</button> : <input style={{backgroundColor: "#4ACF93"}} className="form-control EditMode" type="submit" value="Save"></input>}
                                </div>
                                <div className="RightBlock2">
                                    <div className="NotCalendarEvents">
                                        <h4>Not Assigned Events</h4>
                                        {pageNotCalEvCount === 0 ? <div></div> :
                                            <ul className="list-group">
                                                {notAssignedEvents.map((item, index) => (
                                                    <li className="list-group-item" key={index}><Link to={"/event/" + item._id}>{item.name}</Link></li>
                                                ))}
                                            </ul>}
                                    </div>
                                    {pageNotCalEvCount === 0 ? <div></div> :
                                        <div className="PagBut2">
                                            <div className="prevButtonClEv">
                                                {(pageNotCalEv === 1) ? <button disabled className="form-control">prev</button> : <button className="form-control" onClick={(event) => { event.preventDefault(); prevPageNotEvCal(); }} >prev</button>}
                                            </div>
                                            <p>{pageNotCalEv + ' / ' + pageNotCalEvCount}</p>
                                            <div className="nextButtonClEv">
                                                {((pageNotCalEv) < pageNotCalEvCount) ? <button className="form-control" type="button" onClick={(event) => { event.preventDefault(); nextPageNotEvCal(); }}>next</button> : <button disabled className="form-control">next</button>}
                                            </div>
                                        </div>}
                                    {pageNotCalEvCount === 0 ? <div className="NothingFoundProfile">Nothing&nbsp;Found</div> :
                                        <div className="AddLink">
                                            <select className="form-control" id="selectmy" onChange={(event) => { event.preventDefault(); setEventToLink(event.currentTarget.value); }}>
                                                <option hidden defaultValue >Choose</option>
                                                {notAssignedEvents.map((item, index) => (
                                                    <option key={index} value={item._id}>{item.name}</option>
                                                ))}
                                            </select>
                                        </div>}
                                    {pageNotCalEvCount === 0 ? <div></div> :
                                        <div className="AddButtonCL">
                                            {eventToLink ? <button className="form-control" onClick={(event) => { event.preventDefault(); handleAddLink(); setLoadData(false); setEventToLink(undefined); clearSelected(); }}>Add Link</button> : <button disabled className="form-control">Add Link</button>}
                                        </div>}
                                </div>
                            </div>
                        </Form>
                    );
                }}
            </Formik >

        }
        </div>
    );
}



export default Calendar;
