import React, { useContext, useState, useEffect } from 'react';
import '../style/profile.css'
import { AuthContext } from '../authContext';
import { Formik, Field, Form, ErrorMessage } from 'formik';
import * as Yup from 'yup';



const status = ["user", "admin"];

function Profile({ history, match }) {


  const [editMode, setEditMode] = useState(false);

  const cont = useContext(AuthContext);
  const [user, setUser] = useState([]);

  const [loading, setLoading] = useState(true);

  useEffect(() => {
    async function fetchUser() {
      if (!cont.user)
        history.push('/unauthorized')
      else {
        try {
          let response = await fetch(`/api/v1/user/${match.params.id}`, {
            method: "GET",
            headers: {
              'Accept': 'application/json',
              'Content-Type': 'application/json'
            }
          });
          if (!cont.user.role && cont.user.id !== match.params.id)
            history.push("/forbiden");
          else if (response.status === 200) {
            const json = await response.json();
            setUser(json);
            setEditMode(false);
            setLoading(false)
          } else if (response.status === 400)
            history.push('/wrongreq')
          else if (response.status === 404)
            history.push('/notfound')

        } catch (error) {
          console.log(error);
        }
      }
    }

    if (match.params.id !== null) {
      fetchUser();
    }
  }, [match.params.id, history, cont.user]);

  function bigBlackCock(event) {
    event.preventDefault();
    setEditMode(!editMode);
  }

  return (
    <div>{!loading &&
      <div>
        < Formik
          enableReinitialize={true}
          initialValues={{
            bio: user.bio,
            fullname: user.fullname,
            role: status[user.role],
            telegram: user.telegramLogin
          }}
          validationSchema={
            Yup.object().shape({
              bio: Yup.string()
                .min(4, 'Bio must be at least 4 characters')
                .max(1000, 'Bio shoudn`t be above 1000 characters')
                .required(),
              fullname: Yup.string()
                .min(4, 'Fullname must be at least 4 characters')
                .max(20, 'Fullname should be less thsn 20 characters')
                .required(),
              telegramLogin: Yup.string()
                .min(1, 'Fullname must be at least 4 characters')
                .max(20, 'Fullname should be less thsn 20 characters')
            })
          }
          onSubmit={async (fields) => {
            let status;
            if (fields.role === "admin")
              status = 1;
            else
              status = 0;

            setLoading(true);
            try {
              let formData = new FormData();
              if (fields.file)
                formData.append('avaUrl', fields.file);
              formData.append('fullname', fields.fullname);
              formData.append('bio', fields.bio);
              formData.append('role', status);
              formData.append('telegram', fields.telegram);

              let resualt = await fetch(`/api/v1/user/${match.params.id}`, {
                method: "PUT",
                headers: {
                },
                body: formData
              })
              console.log(resualt);
              fields.file = undefined;
              if (resualt.status === 200) {
                let json = await resualt.json();
                setEditMode(false);
                setUser(json);
                console.log(json);
                setLoading(false);
              } else if (resualt.status === 400) {
                history.push('/wrongreq')
              } else if (resualt.status === 404) {
                history.push('/notfound')
              }

            } catch (err) {
              console.log(err);
            }
          }}
        >
          {({ errors, touched, setFieldValue }) => {
            return (
              <Form>
                <div className="ProfileWrapper">
                  <div className="ProfileLeftBlock">
                    <img src={user.avaUrl} alt="ProfilePhoto"></img>
                    <div className="ProfileChangeAva">
                      {(editMode && user._id === cont.user.id) ? <input type="file" name="file" onChange={(event) => { setFieldValue("file", event.currentTarget.files[0]); }}></input> : <div />}
                    </div>
                    <div className="FullnameInfo">
                      <label htmlFor="fullname"><p>Fullname&nbsp;:&nbsp;</p></label>
                      {(editMode && user._id === cont.user.id) ? <Field name="fullname" className="form-control" component="input" /> : <p>{user.fullname}</p>}
                    </div>
                    <ErrorMessage component="div" className="invalid-feedback error-message ErrorFullname" name="fullname" />
                    <div className="StatusInfo1">
                      <label>Status&nbsp;:&nbsp;</label>
                      {(editMode && cont.user.role === 1 && cont.user.id !== user._id) ? <Field name="role" className="form-control" component="select"><option>user</option><option>admin</option></Field> : <p>{status[user.role]}</p>}
                    </div>
                    <div className="Telegraminfo">
                      <label>Telegram&nbsp;:&nbsp;</label>
                      {(editMode && cont.user.id === user._id) ? <Field name="telegram" className="form-control" component="input"></Field> : <p>{user.telegramLogin}</p>}
                    </div>
                  </div>
                  <div className="ProfileRightBlock">
                    <h1>
                      Your bio
                    </h1>
                    {(editMode && user._id === cont.user.id) ? <Field name="bio" className="form-control" component="textarea"></Field> : <p>{' ' + user.bio}</p>}
                    <ErrorMessage component="div" className="invalid-feedback error-message" name="bio" />
                    {editMode ? <button style={{backgroundColor: "#4ACF93"}} type="submit" className="form-control">Save</button> : <button style={{backgroundColor: "yellow"}} className="form-control" type="button" onClick={bigBlackCock}>Edit Mode</button>}
                  </div>
                </div>
              </Form>
            );
          }}
        </Formik >  </div>} </div>
  );
}

export default Profile;