import React, {useState, useContext} from 'react';
import { AuthContext } from '../authContext';
import '../style/events.css'
import { Formik, Form} from 'formik';
import * as Yup from 'yup';
import * as Datetime from 'react-datetime';
import moment from 'moment';

function AddEvent({history}) {
    let cont = useContext(AuthContext);
    if(!cont.user)
        history.push("/unauthorized");
    const [validate, setValidate] = useState(true);
    
    let yesterday = Datetime.moment().subtract( 1, 'day' );

    let today = Datetime.moment().add(3, "m");
    
    let valid = function (current) {
        return current.isAfter(yesterday);
    };

    function val (inputDate) {
        if(moment(inputDate).isAfter(moment()))
            setValidate(true);
        else 
            setValidate(false);
    }
    return (
        <div>
            <Formik
                initialValues={{ 
                }}
                validationSchema={Yup.object().shape({
                })}
                onSubmit={async (fields) => {
                    try {
                        
                        let time = moment(fields.deadline).format();
                        console.log(time);

                        let formData = new FormData();
                        if (fields.file)
                            formData.append('themeUrl', fields.file);
                        formData.append('description', fields.description);
                        formData.append('name', fields.name);
                        formData.append('difficulty', fields.difficulty);
                        formData.append('deadline', time);
                        let resualt = await fetch(`/api/v1/events`, {
                            method: "POST",
                            headers: {
                            },
                            body: formData
                        })
                        console.log(resualt);
                        if (resualt.status === 201) {
                            let json = await resualt.json();
                            console.log(json);
                            history.push(`/event/${ json._id}`);
                        } else if (resualt.status === 400) {
                            history.push('/wrongreq')
                        } else if (resualt.status === 404) {
                            history.push('/notfound')
                        }
                    } catch (err) {
                        console.log(err);
                    }
                }}

            >
                {({ errors, status, touched, setFieldValue}) => {
                    return (
                        <div className="AddEventWrapper">
                            <Form >
                                <div className="Input">
                                <label htmlFor="name">Name&nbsp;:&nbsp;</label>
                                <input name="name" className="form-control" type="text" minLength="4" maxLength="20" required onChange={(event) => { setFieldValue("name", event.currentTarget.value);}}/>

                                <label htmlFor="difficulty">Difficulty&nbsp;:&nbsp;</label>
                                <input name="difficulty"  onChange={(event) => { setFieldValue("difficulty", event.currentTarget.value); }} className="form-control" type="number" min="1" max="10" required/>
                                
                                <label htmlFor="description">Description&nbsp;:&nbsp;</label>
                                <input type="textarea" name="description"   onChange={(event) => { setFieldValue("description", event.currentTarget.value); }} className="form-control" minLength="10" maxLength="200" required />

                                <label htmlFor="deadline">Deadline&nbsp;:&nbsp;</label>
                                <Datetime required isValidDate={ valid } defaultValue={ today }  inputProps={{ disabled: true }} onChange={(date) => { setFieldValue("deadline", date._d); console.log(date._d);  val(date._d)}} />
                                
                                <label htmlFor="deadline">Event&nbsp;Theme&nbsp;:&nbsp;</label>
                                <input type="file" name="file" onChange={(event) => { setFieldValue("file", event.currentTarget.files[0]); }}></input>
                                {validate ? <button type="submit" className="form-control">Save</button> : <button type="submit" disabled className="form-control">Save</button>}
                                </div>
                                
                            </Form>
                        </div>
                    );
                }}
            </Formik>
        </div>
    );
}



export default AddEvent;