import React from 'react';
import '../style/events.css'
import { Link } from 'react-router-dom';

function EventTableItem({ item, index, page }) {
    const id = (page - 1) * 5 + index + 1;
    let backCol = "";
    if(item.status === -1)
        backCol = "#FF4E4E";
    else if(item.status === 1)
        backCol = "#66FF66"

    return (
        <tr>
            <th style={{background : backCol}} scope="row">{id}</th>
            <td style={{background : backCol}}><Link to={`/event/` + item._id}>{item.name}</Link></td>
            <td style={{background : backCol}}>{item.deadLine}</td>
        </tr>
    );
}



export default EventTableItem;