import React, { useContext } from 'react';
import '../style/login.css'
import { Formik, Field, Form, ErrorMessage } from 'formik';
import * as Yup from 'yup';
import { AuthContext } from '../authContext';

function LogOrPassIncor({ val }) {
    if (!val)
        return (
            <div></div>
        )
    else
        return (
            <div stylename="error-message">
                <p>
                    Incorrect password or login
                </p>
            </div>
        )
}




function Login({ history }) {
    
    const cont = useContext(AuthContext);
    let err = false;

    if(cont.user)
        history.push("/");

    return (
        <div className="wrapper">
            <Formik
                initialValues={{
                    login: '',
                    password: ''
                }}
                validationSchema={Yup.object().shape({
                    login: Yup.string()
                        .min(4, 'Login must be at least 4 characters')
                        .required('Login is required'),
                    password: Yup.string()
                        .min(6, 'Password must be at least 6 characters')
                        .required('Password is required')
                })}
                onSubmit={async (fields) => {
                    try {
                        let res = await fetch('/api/v1/userauth', {
                            method: "POST",
                            headers: {
                                'Accept': 'application/json',
                                'Content-Type': 'application/json'
                            },
                            body: JSON.stringify({ login: fields.login, password: fields.password })
                        });
                        let resJson = await res.json();
                        console.log(resJson.user);
                        if (resJson.user) {
                            cont.handleLogIn({ user: { name: resJson.user.login, isAuthenticated: true, role: resJson.user.role, id: resJson.user.id}});
                            history.push("/")
                        }
                        else 
                            err = true;
                    }
                    catch (err) {
                        console.log(err);
                    }
                }}

            >
                {({ errors, status, touched }) => {
                    return (
                        <Form onChange={() => err = false}>
                            <div className="form-group">
                                <label htmlFor="login">Login</label>
                                <Field name="login" type="text" className={'form-control' + (errors.login && touched.login ? ' is-invalid' : '')} />
                                <ErrorMessage name="login" component="div" className="invalid-feedback error-message" />
                            </div>
                            <div className="form-group">
                                <label htmlFor="password">Password</label>
                                <Field name="password" type="password" className={'form-control' + (errors.password && touched.password ? ' is-invalid' : '')} />
                                <ErrorMessage name="password" component="div" className="invalid-feedback error-message" />
                            </div>
                            <LogOrPassIncor val={err} />
                            <div className="form-group">
                                <button type="submit" className="btn btn-primary mr-2">Login</button>
                            </div>
                        </Form>
                    );
                }}
            </Formik>

        </div>
    )
}

export default Login;



