import React from 'react';
import { Formik, Field, Form, ErrorMessage } from 'formik';
import * as Yup from 'yup';
import '../style/register.css'

function ErrorMessages({ val }) {
    if (val === "NoErrors")
        return (
            <div></div>
        )
    else if (val === "PasswordsDontMatch")
        return (
            <div stylename="error-message">
                <p>
                    Passwords don`t match
                </p>
            </div>
        )
    else if (val === "SuchLoginExists")
        return (
            <div stylename="error-message">
                <p>
                    Such Login Exists
                </p>
            </div>
        )
}

function Register({ history }) {

    let err = "NoErrors";
    return (
        <div className="wrapper">
            <Formik
                initialValues={{
                    login: '',
                    password: '',
                    passwordConfirm: '',
                }}
                validationSchema={Yup.object().shape({
                    login: Yup.string()
                        .min(4, 'Login must be at least 4 characters')
                        .required('Login is required'),
                    password: Yup.string()
                        .min(6, 'Password must be at least 6 characters')
                        .required('Password is required'),
                    passwordConfirm: Yup.string()
                        .min(6, 'Password must be at least 6 characters')
                        .required('Password is required')
                })}
                onSubmit={async (fields) => {
                    if (fields.password !== fields.passwordConfirm)
                        err = "PasswordsDontMatch";
                    else {
                        try {
                            let res = await fetch('api/v1/user', {
                                method: "POST",
                                headers: {
                                    'Accept': 'application/json',
                                    'Content-Type': 'application/json'
                                },
                                body: JSON.stringify({ login: fields.login, password: fields.password })
                            });
                            console.log(res);
                            if (res.status === 400)
                                err = "SuchLoginExists";
                            else
                                history.push("/login");

                        } catch (err) {
                            console.log(err);
                        }
                    }
                }}

            >
                {({ errors, status, touched }) => {
                    return (
                        <Form onChange={() => err = "NoErrors"}>
                            <div className="form-group">
                                <label htmlFor="login">Login</label>
                                <Field name="login" type="text" className={'form-control' + (errors.login && touched.login ? ' is-invalid' : '')} />
                                <ErrorMessage name="login" component="div" className="invalid-feedback error-message" />
                            </div>
                            <div className="form-group">
                                <label htmlFor="password">Password</label>
                                <Field name="password" type="password" className={'form-control' + (errors.password && touched.password ? ' is-invalid' : '')} />
                                <ErrorMessage name="password" component="div" className="invalid-feedback error-message" />
                            </div>
                            <div className="form-group">
                                <label htmlFor="password">Password Confirmation</label>
                                <Field name="passwordConfirm" type="password" className={'form-control' + (errors.password && touched.password ? ' is-invalid' : '')} />
                                <ErrorMessage name="passwordConfirm" component="div" className="invalid-feedback error-message" />
                            </div>
                            <ErrorMessages val={err} />
                            <div className="form-group">
                                <button type="submit" className="btn btn-primary mr-2">Register</button>
                            </div>
                        </Form>
                    );
                }}
            </Formik>

        </div>
    );
}

export default Register;