import React from 'react';
import '../style/events.css'
import { Link } from 'react-router-dom';

function CalendarTableItem({ item, index, page }) {
    const id = (page - 1) * 5 + index + 1;
    return (
        <tr>
            <th scope="row">{id}</th>
            <td><Link to={`/calendar/` + item._id}>{item.name}</Link></td>
            <td>{item.creationDate}</td>
        </tr>
    );
}



export default CalendarTableItem;